.home-search {
  background: rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 10px -4px #000;
  margin: 0 auto 10px auto;
  padding: 10px;
  position: relative;
  width: 100%;
  .block {
    background: #fff;
    box-shadow: 0 0 3px #ccc;
    min-height: 100px;
    padding: 5px 10px;
    h2 {
      margin: 0 0 10px 0;
    }
    .note {
      background-color: #f0f0f0;
      margin: 25px 0 0 0;
      padding: 5px;
    }
  }
}

.arrow {
  display: block;
  left: 0;
  margin: 0 auto 10px auto;
  position: relative;
  right: 0;
  top: 0;
  width: 100px;
}